@import '../../partials/mixins.scss';


.navbar {
  background-color: #222930;
  height: 5rem;
  display: flex;
 
  align-items: center;

  @include desktop {
    height: 5rem;
  }
}


.menu-bars {
  margin-left: 1rem;
  font-size: 1.25rem;
  background: none;

  @include desktop {
    font-size: 1.5rem;
  }
}

.nav-menu {
  background-color: #252d35;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
  z-index: 99999;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #3b74ad;
}

.nav-menu-items {
  width: 100%;
  z-index: 99999;
}

.navbar-toggle {
  background-color: #252d35;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.wrapper {
  display: flex;
  justify-content: space-evenly;
  background-color: #222930;
}

.current-nav-user {
  position: relative;
  display: flex; 
  // box-shadow: 5px 10px 18px #888888;
  text-decoration: none;
  background-color: #222930;
  color: white;
  width: 50%;
  height: 5%;
  padding-left: 3.5rem;
  border-left: solid 1px#888888;

  @include tablet {
    padding-left: 10.5rem;
  }

  @include desktop {
    padding-left: 17rem;
    height: 4rem;
    padding-bottom: 1rem;
  }  
  
  
} 

.nav-link-text {
  color: white;
  font-size: 1rem;
  text-align: center;
  margin-left: auto;
  margin-right: auto;

  @include desktop {
    font-size: 1.5rem;
    
  }
}

.current-nav-user-two {
  position: relative;
  display: flex;
  
  // box-shadow: 5px 10px 18px #888888;
  text-decoration: none;
  background-color: #222930;
  color: white;
  width: 50%;
  height: 5%;
  padding-left: 2.5rem;

  @include tablet {
    padding-left: 10.5rem;
  }
  @include desktop {
    padding-left: 20rem;
    height: 4rem;
    padding-bottom: 1rem;
  }  
  
  
}

.current-nav-flex {
  display: flex;
  flex-direction: row-reverse;
  text-align: center;
  
}

.current-nav-user-two:hover, .current-nav-user:hover {
  background-color: #3b74ad;
}

.logo-transparent {
  
    top: 18.5rem;
    right: 1rem;
    position: relative;
    height: 12rem;
  
}

.header__band {
  font-family: "Guaraldo";
  font-size: .45rem;
  margin-top: 3.5rem;
  color: #5d7996;
  position: absolute;
  right: .25rem;
  top: -1.35rem;
  

  @include tablet {
    font-size: 1rem;
    
  }
}

.logout-flex {
  display: flex;
  position: relative;
  left: 1.25rem;
  text-decoration: none;
  top: 30vh;
}

.logout-text {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1.05rem;
  z-index: 99999;
  padding: 8px 0px 8px 16px;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.logout-text a:hover {
  background-color: #3b74ad;
}
