@import "../../partials/mixins.scss";

.footer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 8rem;
    width: 100%;
    background-color: #222930;

    @include tablet {
        flex-direction: row;
        
    }
    

    &__tagline {
        z-index: 9999;    
        margin: auto;
        position: relative;
        width: 100%;
        
        @include tablet {
            width: 50%;
        }
       
      
    }

    &__left{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 50%;
        margin-left: 5rem;
        margin-top: 1.5rem;

        @include tablet {
            margin-left: 0;
            justify-content: space-evenly;
            margin-top: 3.25rem;
        }
        
    }

    &__right {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 50%;
        margin-left: 5rem;

        @include tablet {
            margin-left: 0;
            justify-content: space-evenly;
            margin-top: 3.25rem;
        }
        
    }

